import React from 'react';
import PropTypes from 'prop-types';
import SharedPropTypes from 'helpers/propTypes';
import cn from 'classnames';
import getTileAriaLabel from '@itv/fe-components/cjs/core/utils/get-tile-aria-label';
import Tile from 'components/Tile';
import getImages from 'helpers/getImages';
import MemoLink from 'components/MemoLink';
import './CategoryTile.scss';

const CategoryTile = ({ programme, className, id, position, link, backTo, throttle }) => {
    const { title, image, channel, episodeCount } = programme;
    const images = getImages(image, 'basicTile');

    const ariaLabelDetails = {
        title: programme.title,
        channel: programme.channel
    };

    const ariaLabel = getTileAriaLabel(ariaLabelDetails);

    return (
        <MemoLink
            className={cn(className, 'CategoryTile')}
            backTo={backTo}
            id={id}
            position={position}
            to={link}
            throttle={throttle}
            ariaLabel={ariaLabel}
        >
            <Tile className={className} title={title} images={images} channel={channel} episodeCount={episodeCount} />
        </MemoLink>
    );
};

CategoryTile.defaultProps = {
    className: '',
    position: null,
    id: null,
    backTo: null,
    programme: {
        episodeCount: null
    },
    throttle: {
        horizontal: 0,
        vertical: 0
    }
};

CategoryTile.propTypes = {
    id: PropTypes.string,
    link: PropTypes.string.isRequired,
    programme: PropTypes.shape({
        title: PropTypes.string.isRequired,
        image: PropTypes.string.isRequired,
        channel: PropTypes.string.isRequired,
        episodeCount: PropTypes.number
    }),
    className: PropTypes.string,
    position: PropTypes.number,
    backTo: SharedPropTypes.backTo,
    throttle: PropTypes.shape({
        horizontal: PropTypes.number,
        vertical: PropTypes.number
    })
};

export default CategoryTile;
