import React from 'react';
import PropTypes from 'prop-types';
import { useDevice } from 'contexts/DeviceContext';
import Slider from 'components/Slider';
import ViewAllTile from 'components/ViewAllTile';
import categoryData from 'constants/categoryData';
import CategoryTile from './CategoryTile';

import './CategorySlider.scss';

const CategorySlider = ({ category, programmes, excludeId, id, testId, writeToDOM }) => {
    const { hasModule } = useDevice();
    const isCategoriesEnabled = hasModule('categories');
    const filteredProgrammes = programmes.filter((programme) => programme.programmeId !== excludeId).slice(0, 12);

    const categorySlug = categoryData.find((item) => item.title === category)?.slug;

    if (filteredProgrammes.length < 2 || !writeToDOM) return null;

    return (
        <Slider
            header={{ title: `More ${category}` }}
            id={id}
            className="CategorySlider"
            testId={testId}
            optimiseRenders
            items={filteredProgrammes
                .map((programme) => {
                    const { encodedEpisodeId, programmeId } = programme;

                    const tileId = `${category}-${encodedEpisodeId.letterA}`;

                    return (
                        <CategoryTile
                            key={encodedEpisodeId.letterA}
                            id={tileId}
                            link={`/programmes/${programmeId}`}
                            programme={programme}
                            category={category}
                            contentType="related-episode"
                            throttle={{
                                horizontal: 200,
                                vertical: 0
                            }}
                        />
                    );
                })
                .concat(
                    isCategoriesEnabled ? (
                        <ViewAllTile category={category} id={`${category}-view-all`} link={`/categories/${categorySlug}`}>
                            View all
                            <div className="CategorySlider__category">{category}</div>
                        </ViewAllTile>
                    ) : (
                        []
                    )
                )}
        />
    );
};

CategorySlider.defaultProps = {
    testId: undefined,
    writeToDOM: true
};

CategorySlider.propTypes = {
    id: PropTypes.string.isRequired,
    category: PropTypes.string.isRequired,
    excludeId: PropTypes.string.isRequired,
    programmes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    testId: PropTypes.string,
    writeToDOM: PropTypes.bool
};

export default CategorySlider;
