import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import SharedPropTypes from 'helpers/propTypes';
import isFunction from 'lodash/fp/isFunction';
import { usePreviewUpdater } from 'contexts/PreviewContext';
import MemoLink from 'components/MemoLink';
import './ViewAllTile.scss';

const ViewAllTile = ({ link, category, children, id, position, backTo, throttle, onClick }) => {
    const setPreview = usePreviewUpdater();

    const onViewAllFocus = useCallback(
        () => {
            if (isFunction(setPreview)) {
                setPreview({
                    programme: {
                        categories: [category]
                    },
                    contentType: 'viewAll',
                    animate: true
                });
            }
        },
        [category] // eslint-disable-line react-hooks/exhaustive-deps
    );

    const onViewAllClick = useCallback(() => {
        setPreview(null);
        onClick();
    }, [setPreview, onClick]);

    return (
        <MemoLink
            className="ViewAllTile"
            onFocus={onViewAllFocus}
            id={id}
            to={link}
            position={position}
            backTo={backTo}
            throttle={throttle}
            onClick={onViewAllClick}
        >
            <div className="ViewAllTile__wrapper">
                <div className="ViewAllTile__content">
                    <div className="ViewAllTile__copy">{children}</div>
                </div>
            </div>
        </MemoLink>
    );
};

ViewAllTile.defaultProps = {
    position: null,
    backTo: null,
    throttle: {
        horizontal: 0,
        vertical: 0
    },
    onClick: () => null
};

ViewAllTile.propTypes = {
    category: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    children: PropTypes.node.isRequired,
    id: PropTypes.string.isRequired,
    position: PropTypes.number,
    backTo: SharedPropTypes.backTo,
    throttle: PropTypes.shape({
        horizontal: PropTypes.number,
        vertical: PropTypes.number
    }),
    onClick: PropTypes.func
};

export default ViewAllTile;
