import { useEffect, useState, useMemo, useCallback } from 'react';
import { SeriesModes } from '../helpers/constants';

const { manySeries, oneSeries, film, noSeries } = SeriesModes;

const useSeries = (seriesList = [], episodeId, programme) => {
    const [originalSeries, setOriginalSeries] = useState();
    const [selectedSeries, setSelectedSeries] = useState(null);
    const [seriesMode, setSeriesMode] = useState(null);

    const { isSeries, isFilms, isLongRunning } = useMemo(() => {
        if (seriesList?.some(({ longRunning }) => longRunning === true)) {
            return {
                isSeries: false,
                isFilms: false,
                isLongRunning: true
            };
        }

        if (seriesList?.some(({ seriesType }) => seriesType === 'SERIES' || seriesType === 'EPISODE')) {
            return {
                isSeries: true,
                isFilms: false,
                isLongRunning: false
            };
        }

        if (seriesList?.some(({ seriesType }) => seriesType === 'FILM')) {
            return {
                isSeries: false,
                isFilms: true,
                isLongRunning: false
            };
        }

        return {
            isSeries: false,
            isFilms: false,
            isLongRunning: false
        };
    }, [seriesList]);

    const episode = useMemo(() => {
        let foundEpisode;
        if (episodeId !== undefined) {
            seriesList?.forEach(({ titles }) => {
                foundEpisode = !foundEpisode && titles?.find((title) => title.encodedEpisodeId.letterA === episodeId);
            });
        }
        return foundEpisode;
    }, [episodeId, seriesList]);

    const seriesNumberList = useMemo(() => {
        let list;
        if (seriesList) {
            list = seriesList.map((e) => e.seriesNumber);
        }
        return list;
    }, [seriesList]);

    const getIsFullSeries = useCallback(() => {
        const season = seriesList?.find((e) => e.seriesNumber === selectedSeries);
        return season?.fullSeries;
    }, [selectedSeries, seriesList]);

    const filteredProductions = useMemo(() => {
        const data = seriesList?.find((e) => e.seriesNumber === selectedSeries)?.titles || [];

        if (isLongRunning) {
            const copy = data.slice(0);
            return copy.reverse();
        }

        return data;
    }, [seriesList, isLongRunning, selectedSeries]);

    const episodeHeroData = useMemo(() => {
        const getEpisodeForHeroData = () => {
            let production;
            seriesList?.forEach(({ titles }) => {
                if (!production) {
                    production = titles?.find(({ encodedEpisodeId }) => episodeId && episodeId === encodedEpisodeId.letterA);
                }
            });

            if (production) {
                return production;
            }
            if (isLongRunning) {
                const lastSeason = seriesList[seriesList?.length - 1];
                const lastEpisode = lastSeason?.titles[lastSeason?.titles?.length - 1];
                return lastEpisode;
            }
            return seriesList[0]?.titles[0];
        };
        const data = getEpisodeForHeroData();
        if (data) {
            data.accessibilityTags = [];
            if (data?.audioDescriptionUrl) data.accessibilityTags.push('ad');
            if (data?.visuallySigned) data.accessibilityTags.push('bsl');
            if (data?.subtitlesLocation) data.accessibilityTags.push('s');
            data.contentInfoType = ['programme', 'series', 'episode'].includes(data?.productionType?.toLowerCase())
                ? 'programme'
                : data?.productionType?.toLowerCase();
            data.numberOfAvailableSeries = programme?.numberOfAvailableSeries;
            data.title = programme?.title || data?.title;
            data.longDescription = programme?.longDescription || data?.longDescription;
            data.image = programme?.image || data?.image;
        }
        return data;
    }, [episodeId, seriesList, isLongRunning, programme]);

    useEffect(() => {
        if (isSeries) {
            setSeriesMode(seriesNumberList?.length > 1 ? manySeries : oneSeries);
            setSelectedSeries(seriesNumberList[0]);
        } else if (isFilms) {
            setSeriesMode(film);
        } else if (isLongRunning) {
            setSeriesMode(seriesNumberList?.length > 1 ? manySeries : oneSeries);
            setSelectedSeries(seriesNumberList[seriesNumberList?.length - 1]);
        } else {
            setSeriesMode(noSeries);
            setOriginalSeries(null);
        }
    }, [isSeries, isLongRunning, isFilms, episode, seriesNumberList]);

    useEffect(() => {
        if (!originalSeries) {
            // The hero and CTA should not change when changing series, to ensure this
            // we must use the original series...
            setOriginalSeries(selectedSeries);
        }
    }, [selectedSeries, originalSeries]);

    return {
        filteredProductions,
        seriesNumberList,
        selectedSeries,
        setSelectedSeries,
        seriesMode,
        getIsFullSeries,
        episodeHeroData,
        isLongRunning
    };
};

export default useSeries;
