import React, { memo } from 'react';
import PropTypes from 'prop-types';
import Link from 'components/Link';
import { paths } from 'routes/constants';
import { cptSend } from 'utils/tracking/cpt';
import { CptEvents } from 'utils/tracking';
import { useDevice } from 'contexts/DeviceContext';
import { Banners } from './constants';

import './PremiumBanner.scss';

const PremiumBanner = ({ id, insertAfterId, userHadTrial, writeToDOM, backTo }) => {
    const { staticImages } = useDevice();
    const bannerImage = `${staticImages}/images/premium/ctv/${userHadTrial ? Banners.NO_TRIAL : Banners.TRIAL}`;

    const trackClick = () => {
        cptSend({
            event: userHadTrial ? CptEvents.UPGRADE_BANNER_CLICK_EVENT : CptEvents.TRIAL_BANNER_CLICK_EVENT
        });
    };

    return (
        <Link to={paths.premiumUpsell} className="PremiumBanner" id={id} insertAfterId={insertAfterId} onClick={trackClick} backTo={backTo}>
            {writeToDOM && (
                <div id={`${id}-container`} className="PremiumBanner__container" style={{ backgroundImage: `url(${bannerImage})` }} />
            )}
        </Link>
    );
};

PremiumBanner.defaultProps = {
    writeToDOM: true,
    backTo: null
};

PremiumBanner.propTypes = {
    id: PropTypes.string.isRequired,
    insertAfterId: PropTypes.string.isRequired,
    userHadTrial: PropTypes.bool.isRequired,
    writeToDOM: PropTypes.bool,
    backTo: PropTypes.func
};

const PremiumBannerMemo = memo(
    PremiumBanner,
    (prevProps, nextProps) =>
        // Keep this PremiumBanner instance as is if:
        //  * The className prop has not changed AND
        //  * The writeToDOM prop has not changed
        //  * The insertAfterId prop has not changed
        nextProps.className === prevProps.className &&
        nextProps.writeToDOM === prevProps.writeToDOM &&
        nextProps.insertAfterId === prevProps.insertAfterId
);

PremiumBannerMemo.displayName = 'PremiumBannerMemo';

export default PremiumBannerMemo;
