import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { grid, gridWrapper } from '@itv/fe-components/cjs/common/styles/grid.cls';
import { SeriesModes } from '../helpers/constants';
import './EpisodesHeader.scss';

const EpisodesHeader = ({ seriesMode, selectedSeries, productionsCount, isFullSeries, isLongRunning }) => {
    const headerText = `${productionsCount} Episode${productionsCount > 1 ? 's' : ''}`;

    return (
        <div className={cn('EpisodesHeader', grid, gridWrapper)} id="episode-header">
            {isFullSeries && (
                <div className="EpisodesHeader__fullSeriesLabel" id="full-series-label">
                    Full Series
                </div>
            )}
            {(seriesMode === SeriesModes.noSeries || isLongRunning) && headerText}
            {(!isLongRunning && seriesMode === SeriesModes.oneSeries) || seriesMode === SeriesModes.manySeries ? (
                <>
                    {selectedSeries !== 'undefined' ? `Series ${selectedSeries}` : 'Other episodes'} - &nbsp;{' '}
                    <span className="secondary">{headerText}</span>
                </>
            ) : null}
        </div>
    );
};

EpisodesHeader.defaultProps = {
    selectedSeries: null,
    productionsCount: null,
    isFullSeries: false,
    isLongRunning: false
};

EpisodesHeader.propTypes = {
    seriesMode: PropTypes.string.isRequired,
    selectedSeries: PropTypes.string,
    productionsCount: PropTypes.number,
    isFullSeries: PropTypes.bool,
    isLongRunning: PropTypes.bool
};

export default EpisodesHeader;
