/* eslint-disable react/jsx-props-no-spreading */

import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import getWriteToDOM from '../../helpers/getWriteToDom';
import useScrollableList from './useScrollableList';
import FocusFrame from '../FocusFrame';
import './ScrollableList.scss';

function ScrollableList({ listData, resetOnFocusIds }) {
    const { listContainerRef, shouldAnimate, activeRowIndex, numberRowsToShow, activeRowType } = useScrollableList({
        listData,
        resetOnFocusIds
    });

    return (
        <>
            <FocusFrame activeRowType={activeRowType} />
            <section
                ref={listContainerRef}
                id="ScrollableList"
                className={cn('ScrollableList', {
                    ScrollableList__animate: shouldAnimate
                })}
            >
                {listData &&
                    listData.map(({ ListComponent, props: { id, className: rowClass, height, ...other } }, index) => {
                        const writeToDOM = getWriteToDOM({ index, activeRowIndex, numberRowsToShow });

                        const rowId = `${id}Row`;

                        return (
                            <div
                                key={rowId}
                                id={rowId}
                                style={{
                                    height
                                }}
                                className={cn('ScrollableList__row', rowClass, {
                                    ScrollableList__above: index < activeRowIndex
                                })}
                            >
                                <ListComponent id={id} writeToDOM={writeToDOM || index === 0} {...other} />
                            </div>
                        );
                    })}
            </section>
        </>
    );
}

ScrollableList.defaultProps = {
    resetOnFocusIds: []
};

ScrollableList.propTypes = {
    listData: PropTypes.arrayOf(PropTypes.shape).isRequired,
    resetOnFocusIds: PropTypes.arrayOf(PropTypes.string)
};

export default ScrollableList;
