import React, { useRef, useCallback } from 'react';
import PropTypes from 'prop-types';
import TabGroup from 'components/TabGroup';
import { sendElementClick } from 'utils/tracking/cpt';

const TRACKING_ELEMENT_NAME = 'secondary_navigation_series';
const TRACKING_ELEMENT_TYPE = 'tab_group';
const INFO_UNAVAILABLE = 'unavailable';

const getElementDestination = (currentId, prevId) => {
    if (currentId > prevId) return 'right';

    if (currentId < prevId) return 'left';

    return null;
};

const SeriesTabs = ({ id, seriesNumberList, selectedSeries, setSelectedSeries, programmeId, programmeName, backTo }) => {
    const prevTabId = useRef(undefined);
    const seriesListWithLabel = seriesNumberList.map((e) => {
        const label = `${e !== 'others' ? `Series ${e}` : 'Other episodes'}`;
        return { id: e, label };
    });
    const onTabFocus = useCallback(
        (tabId) => {
            setSelectedSeries((prevTab) => {
                prevTabId.current = prevTab;
                return tabId;
            });

            const elementDestination = getElementDestination(tabId, prevTabId.current);
            if (!elementDestination) return;

            sendElementClick(TRACKING_ELEMENT_NAME, TRACKING_ELEMENT_TYPE, elementDestination);
            sendElementClick(TRACKING_ELEMENT_NAME, TRACKING_ELEMENT_TYPE, `/series_${tabId}`, {
                programme: {
                    id: programmeId || INFO_UNAVAILABLE,
                    name: programmeName || INFO_UNAVAILABLE
                }
            });
        },
        [programmeId, programmeName, setSelectedSeries]
    );

    return (
        <TabGroup
            id={id}
            testId="series-tabs"
            className="SeriesTabs"
            tabSelectionType="auto"
            tabsList={seriesListWithLabel}
            onTabSelect={onTabFocus}
            backTo={backTo}
            activeTab={selectedSeries}
        />
    );
};

SeriesTabs.displayName = 'SeriesTabs';

SeriesTabs.defaultProps = {
    programmeId: '',
    programmeName: ''
};

SeriesTabs.propTypes = {
    id: PropTypes.string.isRequired,
    selectedSeries: PropTypes.string.isRequired,
    seriesNumberList: PropTypes.arrayOf(PropTypes.string).isRequired,
    setSelectedSeries: PropTypes.func.isRequired,
    programmeId: PropTypes.string,
    programmeName: PropTypes.string,
    backTo: PropTypes.func.isRequired
};

export default SeriesTabs;
