import { useMemo, useCallback } from 'react';
import PremiumBanner from '../PremiumBanner';
import Episode from '../Episode';
import CategorySlider from '../CategorySlider';

const EPISODE_HEIGHT = 204;
const SPACING_05 = 16;

const useProgrammeListData = ({
    isProgrammeAvailable,
    productionsWithBanner,
    userHadTrial,
    programme,
    progressList,
    isLoggedIn,
    categoryProgrammes,
    selectedSeries,
    episodeHeroData,
    programmeId,
    isChildProfile,
    isProfilesEnabled,
    isPremiumUser
}) => {
    const categorySliderId = `category-slider-${programmeId}-series-${selectedSeries}`;
    const episodeHeroCategories = episodeHeroData?.categories;

    const firstComponentId = `episodeItem-${productionsWithBanner[0]?.encodedEpisodeId?.letterA}`;
    const handlerBackTo = useCallback((componentId) => (componentId !== firstComponentId ? firstComponentId : null), [firstComponentId]);

    return useMemo(
        () =>
            isProgrammeAvailable
                ? [
                      ...productionsWithBanner.map(({ banner, insertAfterId, ...production }) => {
                          const focusId = `episodeItem-${production.encodedEpisodeId?.letterA}`;
                          return banner
                              ? {
                                    ListComponent: PremiumBanner,
                                    props: {
                                        rowType: 'banner',
                                        key: 'PremiumBanner',
                                        id: 'PremiumBanner',
                                        insertAfterId,
                                        height: EPISODE_HEIGHT + SPACING_05,
                                        transformDistanceY: EPISODE_HEIGHT + SPACING_05,
                                        userHadTrial,
                                        backTo: handlerBackTo(focusId)
                                    }
                                }
                              : {
                                    ListComponent: Episode,
                                    props: {
                                        key: `key-${focusId}`,
                                        id: focusId,
                                        production,
                                        programme,
                                        rowType: 'episode',
                                        progressList,
                                        isLoggedIn,
                                        height: EPISODE_HEIGHT + SPACING_05,
                                        transformDistanceY: EPISODE_HEIGHT + SPACING_05,
                                        renderContents: false,
                                        isPremiumUser,
                                        backTo: handlerBackTo(focusId)
                                    }
                                };
                      }),
                      ...(categoryProgrammes?.length && ((isProfilesEnabled && !isChildProfile) || !isProfilesEnabled)
                          ? [
                                {
                                    ListComponent: CategorySlider,
                                    props: {
                                        key: categorySliderId,
                                        id: categorySliderId,
                                        category: episodeHeroCategories[0],
                                        programmes: categoryProgrammes,
                                        excludeId: programmeId,
                                        rowType: 'slider',
                                        height: 275,
                                        noScroll: true,
                                        testId: 'programmePageCatSlider'
                                    }
                                }
                            ]
                          : [])
                  ]
                : [],
        [
            isProgrammeAvailable,
            productionsWithBanner,
            categoryProgrammes,
            isProfilesEnabled,
            isChildProfile,
            categorySliderId,
            episodeHeroCategories,
            programmeId,
            userHadTrial,
            programme,
            progressList,
            isLoggedIn,
            isPremiumUser,
            handlerBackTo
        ]
    );
};

export default useProgrammeListData;
