import { useState, useEffect } from 'react';
import { feeds } from '@itv/fe-cerberus';
import { useNavActions } from 'contexts/NavContext';

const useProgramme = (programmeId) => {
    const { getProgrammesByCategory } = feeds.discovery;
    const { getNewProductions } = feeds.contentInventory;

    const [networkError, setNetworkError] = useState(false);
    const [loading, setLoading] = useState(true);
    const [programme, setProgramme] = useState(null);
    const [seriesList, setSeriesList] = useState([]);
    const [categoryProgrammes, setCategoryProgrammes] = useState(null);
    const { setPageLoading } = useNavActions();

    useEffect(() => {
        setLoading(true);
        setPageLoading(true);
        (async () => {
            let data;
            try {
                data = await getNewProductions(programmeId);
            } catch (error) {
                setNetworkError(true);
                setLoading(false);
                setPageLoading(false);
                console.error(error); // eslint-disable-line no-console
                return;
            }
            setProgramme(data.programme);

            setSeriesList(data.seriesList);

            if (data.seriesList[0]?.categories?.[0]) {
                try {
                    const categoryData = await getProgrammesByCategory(data.seriesList[0].categories[0]);

                    setCategoryProgrammes(categoryData);
                } catch (err) {
                    console.error(err); // eslint-disable-line no-console
                }
            }

            setLoading(false);
            setPageLoading(false);
        })();
    }, [programmeId, getProgrammesByCategory, getNewProductions, setPageLoading]);

    return { programme, seriesList, categoryProgrammes, loading, networkError };
};

export default useProgramme;
