import { useEffect, useRef } from 'react';
import { setTransformStyles } from 'helpers/transformStyleUtils';
import useScrollable from '../ScrollableRows/hooks';

const useScrollableList = ({ listData, resetOnFocusIds }) => {
    const { topPosition, shouldAnimate, activeRowIndex, activeRowType } = useScrollable(listData, resetOnFocusIds);

    const listContainerRef = useRef();

    useEffect(() => {
        if (listContainerRef?.current) {
            setTransformStyles(listContainerRef.current, `translateY(${-topPosition}px)`);
        }
    }, [topPosition]);

    return { numberRowsToShow: 4, shouldAnimate, activeRowIndex, listContainerRef, activeRowType };
};

export default useScrollableList;
