import React, { memo } from 'react';
import PropTypes from 'prop-types';
import Picture from '@itv/fe-components/Picture';
import Icon from '@itv/fe-components/Icon';
import Tag from '@itv/fe-components/Tag';
import clockIcon from '@itv/fe-components/icons/clock';
import ProgressIndicator from '@itv/fe-components/ProgressIndicator';
import formatDateRelative from 'helpers/formatDateRelative';
import { ProductionPropType, ProgrammePropType } from 'helpers/propTypes';
import getImages from 'helpers/getImages';
import PlayerChecksTrigger from 'components/PlayerChecksTrigger';
import { useVodLink } from 'pages/Player/hooks';
import isChildrenContent from 'helpers/isChildrenContent';

import './Episode.scss';

const getTitleString = ({ episode, episodeTitle, dateTime, categories, longRunning }, { title: programmeTitle }) => {
    if (longRunning) {
        return formatDateRelative(new Date(dateTime), { onlyDay: true }, '');
    }
    if (episodeTitle) {
        return `${episode ? `${episode}.` : ''} ${episodeTitle}`;
    }
    if (episode) {
        return `Episode ${episode}`;
    }
    if (categories.includes('FILMS') && programmeTitle) {
        return programmeTitle;
    }
    return 'No title';
};

export const formatAvailability = (availability) => {
    const date = availability.replace('Available until ', '').replace(' today', '');
    if (date.endsWith('pm') || date.endsWith('am')) {
        return `Expires at ${date.replace('.', ':')}`;
    }
    return date;
};

const Episode = ({ production, programme, id, progressList = {}, isLoggedIn, renderContents, writeToDOM, isPremiumUser, backTo }) => {
    const {
        image,
        duration,
        description,
        longDescription,
        guidance,
        episodeId,
        productionId,
        visuallySigned,
        audioDescribed,
        subtitled,
        premium
    } = production;
    const sources = getImages(image, 'programme');
    const title = getTitleString(production, programme);
    const progress = progressList[episodeId];
    const vodLink = useVodLink(productionId);
    const { channel, categories } = production;
    const isKidsContent = isChildrenContent(categories, channel, channel);
    const premiumAccess = !isPremiumUser && premium;

    return (
        <div className="Episode" data-testid={`episode-${episodeId}`}>
            <PlayerChecksTrigger
                id={id}
                toPremiumUpsell={premiumAccess}
                to={vodLink}
                guidance={guidance}
                renderContents={writeToDOM || renderContents}
                isKidsContent={isKidsContent}
                backTo={backTo}
            >
                {writeToDOM && (
                    <>
                        <div className="Episode__image">
                            <Picture sources={sources} />
                        </div>
                        <div className="Episode__image-front">
                            {premiumAccess && <Tag name="premium" className="Episode__premium-tag" data-testid="premiumTag" />}
                            {isLoggedIn && progress > 0 && (
                                <div className="Episode__progress">
                                    <ProgressIndicator progress={progress} data-testid="episode-progress-indicator" />
                                </div>
                            )}
                        </div>
                        <div className="Episode__title">{title}</div>
                        <div className="Episode__description">{longDescription.length < 180 ? longDescription : description}</div>
                        <div className="Episode__meta-container">
                            <div className="Episode__tags">
                                {audioDescribed && <Tag name="ad" data-testid="adIcon" />}
                                {visuallySigned && <Tag name="bsl" data-testid="bsIcon" />}
                                {subtitled && <Tag name="s" data-testid="subtitleIcon" />}
                                {guidance && <Tag name="g" data-testid="guidanceIcon" />}
                            </div>
                            <div className="Episode__meta">
                                <div className="Episode__duration">
                                    <Icon className="Episode__duration__clock-icon" src={clockIcon} />
                                    {duration}
                                </div>
                            </div>
                        </div>
                    </>
                )}
            </PlayerChecksTrigger>
        </div>
    );
};

Episode.defaultProps = {
    renderContents: true,
    writeToDOM: true,
    isPremiumUser: false,
    backTo: null
};

Episode.propTypes = {
    id: PropTypes.string.isRequired,
    production: ProductionPropType.isRequired,
    programme: ProgrammePropType.isRequired,
    progressList: PropTypes.objectOf(PropTypes.number).isRequired,
    isLoggedIn: PropTypes.bool.isRequired,
    renderContents: PropTypes.bool,
    writeToDOM: PropTypes.bool,
    isPremiumUser: PropTypes.bool,
    backTo: PropTypes.func
};

const EpisodeMemo = memo(
    Episode,
    (prevProps, nextProps) =>
        // Keep this Episode instance as is if:
        //  * The className prop has not changed AND
        //  * The writeToDOM prop has not changed
        nextProps.className === prevProps.className && nextProps.writeToDOM === prevProps.writeToDOM
);

EpisodeMemo.displayName = 'EpisodeMemo';

export default EpisodeMemo;
