import React, { useState, useEffect, useContext } from 'react';
import UserContext from '@itv/fe-components/UserContext';
import { paths } from 'routes/constants';
import { useParams, useLocation, useHistory } from 'react-router-dom';
import slugify from 'slugify';
import cn from 'classnames';
import { grid, gridWrapper } from '@itv/fe-components/cjs/common/styles/grid.cls';

import EpisodeHero from '@itv/fe-components/EpisodeHero';
import { sizeList } from '@itv/fe-components/Icon';
import PlayIcon from '@itv/fe-components/icons/play';
import MyListButton from '@itv/fe-components/MyListButton';
import ButtonC from '@itv/fe-components/Button';
import Button from 'components/Button';
import PlayerChecksTrigger from 'components/PlayerChecksTrigger/PlayerChecksTrigger';
import { useVodLink } from 'pages/Player/hooks';
import { useDeepLinkContext } from 'contexts/DeepLinkContext';
import { useModal } from 'contexts/ModalContext';
import isChildrenContent from 'helpers/isChildrenContent';
import { objects } from '@itv/fe-cerberus';

import Page from 'components/Page';
import LoadingBar from 'components/LoadingBar';
import FocusGroup from 'focus/FocusGroup';
import NetworkErrorModal from 'components/NetworkErrorModal';
import ScrollableList from 'components/ScrollableList';
import { PageTypes, useCptPageTracking } from 'utils/tracking';
import { PageIds } from 'utils/tracking/constants';
import useTrial from 'hooks/useTrial';
import { useProfiles } from 'contexts/ProfilesContext';
import { useFocusState } from 'focus/FocusProvider';
import useProgramme from './hooks/useProgramme';
import useSeries from './hooks/useSeries';
import useProgrammeListData from './hooks/useProgrammeListData';
import EpisodesHeader from './EpisodesHeader';
import SeriesTabs from './SeriesTabs';
import { SeriesModes } from './helpers/constants';
import useLastWatched from '../Home/hooks/useLastWatched';
import useBanner from './hooks/useBanner';
import NonExistentProgramme from './NonExistentProgramme';
import './Programme.scss';
import ModalPortal from '../../components/ModalPortal';
import ProfileSelectionDialog from '../../components/ProfileSelectionDialog';

const CTA_PREMIUM_LABEL = 'Watch with Premium';

const heroId = 'programmeLetterBox';
const seriesTabsId = 'seriesTabsId';
const episodeItemId = 'episodeItem';
const premiumBannerId = 'PremiumBanner';

const Programme = () => {
    const { programmeId, episodeId } = useParams();
    const { currentFocus } = useFocusState();
    const { pathname } = useLocation();
    const history = useHistory();
    const { progressList, isLoggedIn } = useLastWatched();
    const { trackCptEvent } = useCptPageTracking();
    const { activeProfile, isProfilesEnabled, profiles, selectProfile, mainProfile, kidsProfile } = useProfiles();
    const { programme, seriesList, categoryProgrammes, loading: useProgrammeLoading, networkError } = useProgramme(programmeId);
    const { isDeepLinkJourney } = useDeepLinkContext();

    const [isHeroHidden, setIsHeroHidden] = useState(false);
    const {
        selectedSeries,
        setSelectedSeries,
        seriesNumberList,
        seriesMode,
        getIsFullSeries,
        filteredProductions,
        episodeHeroData,
        isLongRunning
    } = useSeries(seriesList, episodeId, programme);
    const { getIsPremiumUser } = useContext(UserContext);
    const isPremiumUser = getIsPremiumUser();

    const pageFocusGroupId = `programmePage-${programmeId}`;

    const { productionsWithBanner, isBannerLast } = useBanner(
        filteredProductions,
        episodeHeroData?.categories,
        activeProfile?.isChild,
        isProfilesEnabled
    );

    const { loading: userTrialStatusLoading, userHadTrial } = useTrial();

    useEffect(() => {
        if (currentFocus?.parentId === heroId) setIsHeroHidden(false);
        if (currentFocus?.parentId === seriesTabsId) setIsHeroHidden(true);
        if (currentFocus?.id?.startsWith(episodeItemId)) setIsHeroHidden(true);
        if (currentFocus?.id === premiumBannerId) setIsHeroHidden(true);
    }, [currentFocus]);

    const isProgrammeAvailable = programme && programme?.programmeId;

    const listData = useProgrammeListData({
        isProgrammeAvailable,
        productionsWithBanner,
        userHadTrial,
        programme,
        progressList,
        isLoggedIn,
        categoryProgrammes,
        selectedSeries,
        episodeHeroData,
        programmeId,
        isChildProfile: activeProfile?.isChild,
        isProfilesEnabled,
        isPremiumUser
    });

    const isKidsContent = isChildrenContent(episodeHeroData?.categories, episodeHeroData?.channel, episodeHeroData?.channel);
    const [playerButtonFocus, setPlayerButtonFocus] = useState(true);
    const vodLink = useVodLink(episodeHeroData?.productionId);

    useEffect(() => {
        if (isDeepLinkJourney && profiles?.length && !activeProfile) {
            const profileToSet = isKidsContent && kidsProfile ? kidsProfile : mainProfile;
            selectProfile(profileToSet);
        }
    }, [isKidsContent, profiles, activeProfile, kidsProfile, mainProfile, selectProfile, isDeepLinkJourney]);

    const { toggleExitDialog } = useModal();

    const profileProtector = isProfilesEnabled && isLoggedIn && (!profiles || profiles?.length === 0);
    const isPageLoading = useProgrammeLoading || userTrialStatusLoading || profileProtector;

    useEffect(() => {
        if (isPageLoading) return;

        const programmeTitle = programme?.title || 'unavailable';
        const trackingPageId = isProgrammeAvailable
            ? `${slugify(programmeTitle, { lower: true })}.${programmeId}`
            : `unavailable.${programmeId}`;

        const data = isProgrammeAvailable
            ? {
                  programme: {
                      id: programmeId,
                      name: programmeTitle
                  }
              }
            : {};

        trackCptEvent(PageTypes.EPISODE_LIST, trackingPageId, data);
    }, [isPageLoading, programmeId, programme, isProgrammeAvailable, trackCptEvent, seriesNumberList]);

    if (isProfilesEnabled && isLoggedIn && (profiles?.length > 1 || mainProfile?.isPinProtected) && !activeProfile && !isDeepLinkJourney) {
        const proceedSelectProfile = (profile) => {
            if (profile.isChild) {
                history.push(paths.homepage);
            } else {
                history.push(vodLink);
            }
        };

        return (
            <ModalPortal>
                <FocusGroup stealFocus id="programmeProfileSelection" insertBeforeId={`programmePage-${programmeId}`}>
                    <ProfileSelectionDialog onSuccessCallback={proceedSelectProfile} onCancel={() => {}} profiles={profiles} />
                </FocusGroup>
            </ModalPortal>
        );
    }

    if (isPageLoading) return <LoadingBar />;
    if (networkError) return <NetworkErrorModal />;

    const isFullSeries = getIsFullSeries(selectedSeries);
    const heroPremiumAccess = !isPremiumUser && episodeHeroData?.premium;
    return (
        <Page
            className={cn('Programme', { 'Programme--hideHeader': isHeroHidden })}
            trackingRoute={pathname}
            testId="programmePage"
            trackingPageId={PageIds.PROGRAMME}
        >
            <FocusGroup id={pageFocusGroupId} orientation="column" className="Programme__content" jumpToLastFocus stealFocus backToMemory>
                <>
                    {!isProgrammeAvailable ? (
                        <NonExistentProgramme />
                    ) : (
                        <>
                            {episodeHeroData && (
                                <FocusGroup id={heroId} className={cn('Programme__hero')} jumpToLastFocus optimiseRenders>
                                    <EpisodeHero
                                        titleText={episodeHeroData?.title}
                                        description={{ descriptionTextNormal: episodeHeroData?.longDescription }}
                                        image={episodeHeroData?.image}
                                        tagName={heroPremiumAccess ? 'premium' : episodeHeroData?.tagName}
                                        accessibilityTags={episodeHeroData?.accessibilityTags}
                                        contentInfo={objects.helpers.heroContentInfo({
                                            type: episodeHeroData.contentInfoType,
                                            genre: episodeHeroData?.categories?.[0],
                                            duration: episodeHeroData?.duration,
                                            year: new Date(episodeHeroData?.dateTime).getFullYear(),
                                            itemCount: filteredProductions?.length,
                                            seriesList: [...seriesList],
                                            numberOfAvailableSeries: episodeHeroData?.numberOfAvailableSeries
                                        })}
                                        progress={progressList[episodeHeroData?.episodeId]}
                                        guidance={{ guidanceText: episodeHeroData.guidance }}
                                        renderButtons={(primaryButtonClasses) => (
                                            <>
                                                <PlayerChecksTrigger
                                                    toPremiumUpsell={heroPremiumAccess}
                                                    to={vodLink}
                                                    id="playButtonId"
                                                    guidance={episodeHeroData.guidance}
                                                    backTo={isDeepLinkJourney ? toggleExitDialog : history.goBack}
                                                    isKidsContent={isKidsContent}
                                                    onFocus={() => setPlayerButtonFocus(true)}
                                                    onBlur={() => setPlayerButtonFocus(false)}
                                                >
                                                    <ButtonC
                                                        icon={PlayIcon}
                                                        iconSize={sizeList.large}
                                                        className={cn(primaryButtonClasses, { focus: playerButtonFocus })}
                                                    >
                                                        {heroPremiumAccess
                                                            ? CTA_PREMIUM_LABEL
                                                            : objects.helpers.heroCTA({
                                                                  type: episodeHeroData?.productionType?.toLowerCase(),
                                                                  seriesNumber: episodeHeroData?.series,
                                                                  isLongRunning: false,
                                                                  episodeNumber: episodeHeroData?.episode,
                                                                  broadcastDateTime: episodeHeroData?.dateTime
                                                              })}
                                                    </ButtonC>
                                                </PlayerChecksTrigger>
                                                {!activeProfile?.isChild && (
                                                    <MyListButton
                                                        title={programme.title}
                                                        encodedProgrammeId={programme.encodedProgrammeId.underscore}
                                                        buttonComponent={Button}
                                                        className="Programme__cta Programme__myList-button"
                                                    />
                                                )}
                                            </>
                                        )}
                                    />
                                </FocusGroup>
                            )}
                            {seriesMode === SeriesModes.manySeries && (
                                <SeriesTabs
                                    id={seriesTabsId}
                                    seriesNumberList={seriesNumberList}
                                    activeSeries={selectedSeries}
                                    setSelectedSeries={setSelectedSeries}
                                    getIsFullSeries={getIsFullSeries}
                                    backTo={isDeepLinkJourney ? toggleExitDialog : history.goBack}
                                />
                            )}
                            {isHeroHidden && (
                                <EpisodesHeader
                                    seriesMode={seriesMode}
                                    selectedSeries={selectedSeries}
                                    productionsCount={filteredProductions.length}
                                    isFullSeries={isFullSeries}
                                    isLongRunning={isLongRunning}
                                />
                            )}
                            <div
                                className={cn('Programme__scrollable-list-container', grid, gridWrapper, {
                                    'is-banner-last': isBannerLast
                                })}
                            >
                                {listData && <ScrollableList listData={listData} />}
                            </div>
                        </>
                    )}
                </>
            </FocusGroup>
        </Page>
    );
};

export default Programme;
