import { useMemo } from 'react';
import { usePremium } from 'contexts/PremiumContext';
import isChildrenContent from 'helpers/isChildrenContent';

const useBanner = (productions, categories, isChildProfile, isProfilesEnabled) => {
    const { isPremiumEnabled } = usePremium();

    return useMemo(() => {
        const [first, ...rest] = productions;
        const productionsWithBanner = productions;

        if (
            productions.length &&
            isPremiumEnabled &&
            !isChildrenContent(categories) &&
            ((isProfilesEnabled && !isChildProfile) || !isProfilesEnabled)
        ) {
            return {
                productionsWithBanner: [first, { banner: true, insertAfterId: productions[0].episodeId }, ...rest],
                isBannerLast: Boolean(!rest.length)
            };
        }

        return { productionsWithBanner, isBannerLast: false };
    }, [productions, isPremiumEnabled, categories, isProfilesEnabled, isChildProfile]);
};

export default useBanner;
